import React from 'react';
import { useDataLayerValue } from '../StateProvider';
import useGoogleSearch from '../useGoogleSearch';
import { Link } from 'react-router-dom';
import './searchPage.css';
import Search from './Search';
import AppsIcon from '@material-ui/icons/Apps';
import { Avatar } from '@material-ui/core';
import SearchPageHeaderOptions from './SearchPageHeaderOptions';

// import data from '../response';


function SearchPage() {
  const [{ term }, dispatch] = useDataLayerValue();

  const { data } = useGoogleSearch(term);
  console.log('data ', data);

  return (
    <div className='searchPage'>
      <div className='searchPage_header'>
        <div className='searchPage_header_left'>
          <Link to='/'>
            <img
              className='searchPage_header_logo'
              src='data:image/png;base64,'
            /> 
          </Link>
          <div className='searchPage_body'>
            <Search hidebuttons />
            <SearchPageHeaderOptions />
          </div>
        </div>
        <div className='searchPage_header_right'>
          <AppsIcon className='searchPage_app_icon' />
          <Avatar />
        </div>
      </div>
      {true && (
        <div className='searchPage_results'>
          <p className='searchPage_resultsCount'>
            About {data?.searchInformation?.formattedTotalResults} results (
            {data?.searchInformation?.formattedSearchTime} seconds)
          </p>

          {data?.items?.map((item) => (
            <div className='searchPag e_result'>
              {item?.pagemap?.cse_image?.length > 0 &&
                item?.pagemap?.cse_image[0].src && (
                  <img
                    className='searchPage_result_image_logo'
                    src={item?.pagemap?.cse_image[0]?.src}
                  />
                )}
              <a href={item.link}>{item.displayLink}</a>
              <p>
                <a className='searchPage_result_title' href={item.link}>
                  {item.title}
                </a>{' '}
              </p>

              <p className='searchPage_result_snippet'>{item.snippet}</p>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

export default SearchPage;
